.enla {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding-bottom: 0px;
}
.enla a {
  text-decoration: none;
}
.enla .enla-a {
  width: 32px;
  height: 32px;
  object-fit: cover;
  cursor: pointer;
}

@media only screen and (min-width: 768px) {
  .cabecera-mobile {
    display: none;
  }
}
.aside-pie {
  position: sticky;
  bottom: 0;
  z-index: 10;
  border-top: 1.5px solid rgb(54, 54, 54);
  background-color: #000000;
}
.aside-pie .div-pie {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 1rem;
  padding: 10px 20px;
}

@media only screen and (min-width: 768px) {
  .aside-pie {
    display: none;
  }
}
.seccion-principal {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(1, fr);
  gap: 1rem;
}
.seccion-principal .seccion-imagen {
  width: 30%;
  height: 30%;
}